            height: 1.3vh;
            line-height: 1.3vh;
            color: #fff;
            font-weight: 400;
            margin-right: 1.26%;
        }

        .timeBox {
            font-family: 'timerFont', sans-serif;
            font-size: 2.22vh;
            height: 2.22vh;
            line-height: 2.22vh;
            color: #fff;
            font-weight: normal;
            margin-right: 5%;
            vertical-align: bottom;
            transform: translateY(-0.3vh);
        }

        .exitButton {
            width: 11.25%;
            height: 2.69vh;
            border: 1px solid rgba(212,222,236,0.2);
            border-radius: 0.37vh;
            margin-right: 5%;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            transform: translateY(-0.3vh);

            .exitIconBox {
                width: 1.48vh;
                height: 1.48vh;
                .exitIcon {
                    width: 100%;
