//localStorage的操作方法
export const $db = {
    set(obj){
        for(let key in obj){
            window.localStorage.setItem(key, obj[key]);
        }
    },
    get(key){
        return window.localStorage[key];
    },
    clear(){
        window.localStorage.clear();
    }
}

//常用工具函数
export const $u = {
    /**
     * 获取数组中的最大数值和最小数值
     * @param {Array[number]} arr
     */
    getMaxAndMin(arr){
        if(!arr.length){
            return [];
        }else if(arr.length === 1){
            return [arr[0], arr[0]];
        }
        let max = arr[0]
          , min = arr[0];
        for(let i = 1;i < arr.length;i++){
            if(arr[i] > max){
                max = arr[i];
            }else if(arr[i] < min){
                min = arr[i];
            }
        }
        return [min, max];
    },
    /**
     * 限制输入值的大小
     * @param {number} v 输入值
     * @param {number} min 最小值
     * @param {number} max 最大值
     */
    limitVal(v, min, max){
        if(typeof(v) !== 'number'){
            return v;
        }
        let res = v;
        if(res < min){
            res = min;
        }else if(res > max){
            res = max;
        }
        return res;
    },
    //获取时间日期
    formatDate(){
        let date = new Date();
        let y = date.getFullYear()+'';
        let M = date.getMonth() + 1;
        M += '';
        let d = date.getDate()+'';
        let h = date.getHours().toString();
        let m = date.getMinutes().toString();
        let s = date.getSeconds().toString();
        let w = date.getDay().toString();
        let week;
        switch(w){
            case '0':
                week = '星期日';
                break;
            case '1':
                week = '星期一';
                break;
            case '2':
                week = '星期二';
                break;
            case '3':
                week = '星期三';
                break;
            case '4':
                week = '星期四';
                break;
            case '5':
                week = '星期五';
                break;
            case '6':
                week = '星期六';
                break;
            default:
                week = '警告：日期时间获取错误';
        }
        if(M.length < 2){
            M = '0' + M;
        }
        if(d.length < 2){
            d = '0' + d;
        }
        if(h.length < 2){
            h = '0' + h;
        }
        if(m.length < 2){
            m = '0' + m;
        }
        if(s.length < 2){
            s = '0' + s;
        }
        let format = y + '-' + M + '-' + d + ' ' + h + ':' + m + ':' + s;
        return [y,M,d,h,m,s,week,format];//输出数组内容分别为年、月、日、时、分、秒、星期和格式化后的字符串时间日期
    },
    toThousand(num){
        return num.toString().replace(/\d+/, function(n){
            return n.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
        })
    },
    reload(){
        window.location.reload();
    },
    //图片转Base64的方法，是一个异步方法
    getBase64(img, callback) {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }
}